"use client";

import gsap from "gsap";
import { useGSAP } from "@gsap/react";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import { motion, useInView } from "motion/react";
import { useRef } from "react";
import { wrapWordsInSpan } from "@/utils/string";
import useWindowSize from "@/hooks/useWindowSize";
import TextAnimation from "../layout/TextAnimation";

gsap.registerPlugin(ScrollTrigger);

export default function About() {
  const paragraphRef = useRef<HTMLParagraphElement>(null);
  const videoRef = useRef<HTMLVideoElement>(null);

  const { width } = useWindowSize();

  const isVideoInView = useInView(videoRef, {
    once: false,
    margin: "0px 0px -30% 0px",
  });

  useGSAP(() => {
    if (width < 1024) return;

    const paragraph = paragraphRef.current;

    if (!paragraph) return;

    wrapWordsInSpan(paragraph);

    const words = paragraph.querySelectorAll("span");

    words.forEach((word) => {
      word.classList.add("word" + Math.floor(Math.random() * 4));
    });

    document.querySelectorAll("#about .word1").forEach((el) => {
      gsap.to(el, {
        x: "-0.8em",
        ease: "none",
        scrollTrigger: {
          trigger: el, 
          start: "top 80%",
          
          end: "bottom 60%",
          scrub: 0.2, 
        },
      });
    });

    document.querySelectorAll("#about .word2").forEach((el) => {
      gsap.to(el, {
        x: "1.6em", 
        ease: "none",
        scrollTrigger: {
          trigger: el, 
          start: "top 80%",
          end: "bottom 60%",
          scrub: 0.2,
        },
      });
    });

    document.querySelectorAll("#about .word3").forEach((el) => {
      gsap.to(el, {
        x: "-2.4em", 
        ease: "none",
        scrollTrigger: {
          trigger: el,
          start: "top 80%",
          end: "bottom 60%",
          scrub: 0.2,
        },
      });
    });
  }, []);

  return (
    <section
      id="about"
      className="grid grid-cols-12 gap-4 lg:gap-8 pt-56 pb-28 p-4 lg:px-8"
    >
      <div className="flex flex-col col-span-12 lg:col-span-7">
        <TextAnimation>
          <h4 className="font-semibold uppercase mb-4">About me </h4>
        </TextAnimation>

        {/* Mobile video */}
        <div className="lg:hidden col-span-12 aspect-video rounded-lg overflow-hidden mb-4">
          <motion.video
            ref={videoRef}
            initial={{ clipPath: "inset(0 0 100% 0)" }}
            animate={isVideoInView ? { clipPath: "inset(0 0 0 0)" } : {}}
            transition={{ duration: 1.5, ease: [0.16, 1, 0.3, 1] }}
            src="/videos/about.mp4"
            autoPlay
            muted
            loop
            playsInline
            className="pointer-events-none w-full h-full object-cover"
            onError={(e) => console.error("Video loading error:", e)}
            poster="/images/about.jpg"
          />
        </div>

        <p
          ref={paragraphRef}
          className="hidden lg:block text-[clamp(28px,3.5vw,96px)] font-semibold tracking-tight leading-none"
        >
          Driven by the intersection of design and engineering, I shape engaging, purposeful experiences. I obsess over motion, detail, and speed — helping visionary brands craft products that stand out.
        </p>

        <TextAnimation>
          <p
            ref={paragraphRef}
            className="lg:hidden text-[clamp(28px,3.5vw,96px)] font-semibold tracking-tight leading-none"
          >
           Driven by a love for both design and engineering, I craft engaging, meaningful digital experiences. Through careful attention to motion, performance, and the smallest details, I help visionary brands bring their products to life.
          </p>
        </TextAnimation>
      </div>

      {/* Desktop video */}
      <div className="hidden lg:block h-full col-span-5">
        <div className="sticky top-[calc(100vh-20vw-172px)] w-full aspect-video rounded-lg lg:rounded-xl overflow-hidden">
          <video
            src="/videos/about.mp4"
            autoPlay
            muted
            loop
            playsInline
            className="pointer-events-none"
          />
        </div>
      </div>
    </section>
  );
}
