"use client";

import { useRef, useState } from "react";
import gsap from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import { useGSAP } from "@gsap/react";
import { motion } from "motion/react";

gsap.registerPlugin(ScrollTrigger);

export default function Hero() {
  const heroRef = useRef<HTMLDivElement>(null);
  const [bgImage, setBgImage] = useState("/images/hero-bg1.jpg");
  
  // Background images for hover effect
  const projectImages = [
    "/img/hero1.jpg",
    "/img/hero1.jpg",
    "/img/hero1.jpg"
  ];

  useGSAP(() => {
    // ScrollTrigger for darkening effect when scrolling to about section
    ScrollTrigger.create({
      trigger: "#about",
      start: "top bottom",
      end: "top 80%",
      scrub: true,
      onUpdate: (self) => {
        if (heroRef.current) {
          heroRef.current.style.opacity = `${1 - self.progress * 0.7}`;
        }
      }
    });
  });

  return (
    <>
      <section 
        id="hero"
        ref={heroRef}
        className="h-screen relative overflow-hidden transition-opacity duration-700"
        style={{ 
          backgroundImage: `url(${bgImage})`,
          backgroundSize: "cover",
          backgroundPosition: "center"
        }}
      >
        {/* Dark overlay */}
        <div className="absolute inset-0 bg-black/20 z-0"></div>
        
        {/* Middle content */}
        <div className="absolute top-1/2 left-1/2 transform -translate-x-1/2 -translate-y-1/2 text-center z-10">
          <motion.h1 
            initial={{ y: 20, opacity: 0 }}
            animate={{ y: 0, opacity: 1 }}
            transition={{ duration: 0.8, ease: "easeOut" }}
            className="text-6xl md:text-8xl font-bold text-white mb-2"
          >
            Myrtle Pool House
          </motion.h1>
          
          <motion.p 
            initial={{ y: 20, opacity: 0 }}
            animate={{ y: 0, opacity: 1 }}
            transition={{ duration: 0.8, delay: 0.1, ease: "easeOut" }}
            className="text-xl md:text-2xl text-white mb-8"
          >
            2024
          </motion.p>
          
          <motion.a
            href="#"
            className="inline-block text-white text-xl md:text-2xl font-medium border-b border-white pb-1"
            onMouseEnter={() => {
              const randomImage = projectImages[Math.floor(Math.random() * projectImages.length)];
              setBgImage(randomImage);
            }}
            initial={{ y: 20, opacity: 0 }}
            animate={{ y: 0, opacity: 1 }}
            transition={{ duration: 0.8, delay: 0.2, ease: "easeOut" }}
          >
            View project
          </motion.a>
        </div>
        
        {/* Bottom left text */}
        <div className="absolute bottom-8 left-8 max-w-md z-10">
          <p className="text-white text-sm md:text-base">
            The OH Architecture style is defined by strong, solid forms with subtle elegance,
            natural balance and enduring appeal
          </p>
          <p className="text-white text-4xl md:text-6xl font-bold mt-2">
            (SCROLL down) <br />
            Experience <br />
            focused design
          </p>
        </div>
        
        {/* Bottom right scroll indicator */}
        <div className="absolute bottom-8 right-8 z-10">
          <div className="flex items-center">
            <span className="h-px w-16 bg-white mr-2"></span>
            <span className="text-white">Scroll down</span>
          </div>
        </div>
      </section>
    </>
  );
}